/* Camphor
   -------------------------------------------------------------------------------------------- */

/* Camphor Light */
@font-face {
	font-display: swap;
	font-family: "Camphor";
	font-weight: 300;
	src: url("https://fonts.hlxcdn.com/fonts/camphor/CamphorStd-Light.woff");
}

/* Camphor Regular */
@font-face {
	font-display: swap;
	font-family: "Camphor";
	font-weight: 400;
	src: url("https://fonts.hlxcdn.com/fonts/camphor/CamphorStd-Regular.woff");
}

/* Camphor Medium */
@font-face {
	font-display: swap;
	font-family: "Camphor";
	font-weight: 500;
	src: url("https://fonts.hlxcdn.com/fonts/camphor/CamphorStd-Medium.woff");
}

/* Camphor Bold */
@font-face {
	font-display: swap;
	font-family: "Camphor";
	font-weight: 600;
	src: url("https://fonts.hlxcdn.com/fonts/camphor/CamphorStd-Bold.woff");
}

