*,
*:before,
*:after {
  border: 0;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font: inherit;
}

html {
  scroll-behavior: smooth;
}

body {
  font-weight: 400;
  margin: 0;
  font-family: "Camphor";
  font-size: 18px;
  background-color: #ffffff;
  color: #000000;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

h1,
h2,
h3,
h4 {
  color: #000000;
  font-weight: 700;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

._container {
  max-width: 1062px;
  margin: 0 auto;
  padding: 0px 20px;
}

/* Camphor
   -------------------------------------------------------------------------------------------- */
/* Camphor Light */
@font-face {
  font-display: swap;
  font-family: "Camphor";
  font-weight: 300;
  src: url("https://fonts.hlxcdn.com/fonts/camphor/CamphorStd-Light.woff");
}
/* Camphor Regular */
@font-face {
  font-display: swap;
  font-family: "Camphor";
  font-weight: 400;
  src: url("https://fonts.hlxcdn.com/fonts/camphor/CamphorStd-Regular.woff");
}
/* Camphor Medium */
@font-face {
  font-display: swap;
  font-family: "Camphor";
  font-weight: 500;
  src: url("https://fonts.hlxcdn.com/fonts/camphor/CamphorStd-Medium.woff");
}
/* Camphor Bold */
@font-face {
  font-display: swap;
  font-family: "Camphor";
  font-weight: 600;
  src: url("https://fonts.hlxcdn.com/fonts/camphor/CamphorStd-Bold.woff");
}
/* Tiempos Headline
	 -------------------------------------------------------------------------------------------- */
/* Tiempos Headline Regular */
@font-face {
  font-display: swap;
  font-family: "TiemposHeadline";
  font-weight: 400;
  src: url("https://fonts.hlxcdn.com/fonts/tiempos-headline/TiemposHeadlineWeb-Regular.woff");
}
/* TT Norms
	 -------------------------------------------------------------------------------------------- */
/* TT Norms Regular */
@font-face {
  font-display: swap;
  font-family: "TTNorms";
  font-weight: 400;
  src: url("https://fonts.hlxcdn.com/fonts/tt-norms/TTNorms-Regular.woff");
}
/* TT Norms Medium */
@font-face {
  font-display: swap;
  font-family: "TTNorms";
  font-weight: 500;
  src: url("https://fonts.hlxcdn.com/fonts/tt-norms/TTNorms-Medium.woff");
}
/* TT Norms Bold */
@font-face {
  font-display: swap;
  font-family: "TTNorms";
  font-weight: 700;
  src: url("https://fonts.hlxcdn.com/fonts/tt-norms/TTNorms-Bold.woff");
}
.top_bar {
  padding-top: 3px;
  width: 100%;
  position: fixed;
  z-index: 11;
  height: 30px;
  text-align: center;
  color: #ffffff;
  letter-spacing: 1px;
  background-color: #a0c5db;
}
.top_bar a {
  margin-left: 5px;
  text-decoration: underline;
  color: #ffffff;
  font-weight: 600;
}

.closed {
  display: none;
}
.closed i {
  display: nones;
}

.header {
  margin-top: 30px;
  position: fixed;
  z-index: 10;
  height: 60px;
  width: 100%;
}

.header__container {
  display: flex;
  align-items: center;
  margin-top: 16px;
  justify-content: space-between;
}

.logo__img {
  max-width: 100px;
}

.header__menu {
  display: flex;
}

.menu__list {
  display: flex;
  align-items: center;
}

.menu__link {
  margin-right: 40px;
  color: #000000;
}
.menu__link:hover {
  color: #a0c5db;
}

.header__menu__block {
  display: flex;
}

.hamburger {
  display: none;
}

@media (max-width: 768px) {
  .lock {
    overflow: hidden;
  }

  .logo__img {
    margin-left: 10px;
    position: relative;
    z-index: 15;
  }

  .top_bar {
    font-size: 14px;
  }

  .header__menu {
    position: fixed;
    flex-direction: column;
    height: 100%;
    width: 100%;
    background-color: #a0c5db;
    left: 0;
    z-index: 12;
    top: -100%;
  }

  .menu__cart {
    position: relative;
    z-index: 12;
  }

  .menu__list {
    margin-top: 100px;
    flex-direction: column;
    justify-content: space-between;
    min-height: 250px;
    padding-top: 30px;
    padding-left: 40px;
    align-items: flex-start;
  }

  .menu__link {
    font-size: 25px;
    color: #ffffff;
    font-weight: 700;
  }
  .menu__link:hover {
    color: #122e70;
  }

  .active {
    transition: 1s;
    top: 0;
  }

  .hamburger {
    display: block;
    position: relative;
    z-index: 100;
  }
}
.main {
  margin-top: 100px;
}

.panels {
  background-color: #f4f4f4;
}

.panels__title {
  margin: 0 auto;
  padding-top: 60px;
  max-width: 600px;
}
.panels__title h1 {
  text-align: center;
  font-size: 34px;
}

.panels__subtext {
  padding-top: 9px;
  margin: 0 auto;
  max-width: 703px;
  line-height: 24px;
  font-family: TTNorms;
  text-align: center;
}

.panels__menu__list {
  margin-top: 59px;
  display: flex;
  justify-content: center;
}
.panels__menu__list :not(:last-child) {
  margin-right: 50px;
}

.panels__menu__link {
  font-size: 20px;
  color: #ababab;
}
.panels__menu__link:hover {
  opacity: 0.5;
  color: #000000;
}

@media (max-width: 768px) {
  .panels__title {
    padding-top: 30px;
  }

  .panels__menu {
    display: none;
  }
}